        -webkit-transform: scale(1);
                transform: scale(1);
    }

    40% {
        -webkit-transform: scale(.75);
                transform: scale(.75);
    }

    60% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }

    80% {
        -webkit-transform: scale(.75);
                transform: scale(.75);
    }

    100% {
        -webkit-transform: scale(.75);
                transform: scale(.75);
    }
}

